@import "../init";


// Hero
.hero{
  position: relative;
  background-color: $color-primary;
  height: auto;
  padding-top: toRem(136);
  padding-bottom: toRem(154);
  @include media-breakpoint-down(md) {
    padding-top: toRem(114);
    padding-bottom: toRem(96);
  }
  //pattern on cta section dekstop responsive width size more than 768px
  .hero__background {
    position: absolute;
    width: 100vw;
    overflow: hidden;
    pointer-events: none;
    margin: 0 auto;
    z-index: 1;
    &.hero__background--pattern {
      left: 0;
      right: 0;
      top: toRem(150);
      bottom: 0;
      @include media-breakpoint-down(md) {
        display: none;
      }
      .hero__pattern-bg {
        position: absolute;
        right: toRem(-110);
        display: block;
      }
    }
  }
  .hero__content{
    position: relative;
    display: flex;
    z-index: 10;
    flex-direction: column;
    gap: toRem(24);
    .hero__title{
      max-width: toRem(550);
    }
    .hero__subtitle{
      max-width: toRem(419);
    }
  }
}


//Documentation Section
.documentation {
  position: relative;
  z-index: 10;

  .container {
    position: relative;
    padding-top: toRem(120);
    padding-bottom: toRem(120);

    @include media-breakpoint-down(lg) {
      padding-top: toRem(88);
      padding-bottom: toRem(88);
    }

    @include media-breakpoint-down(xs-2) {
      padding-top: toRem(32);
      padding-bottom: toRem(32);
    }
  }

  .documentation__wrapper {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    gap: toRem(40);

    @include media-breakpoint-down(md) {
      flex-direction: column;
      justify-content: flex-start;
      align-items: stretch;
      width: 100%;
      overflow: hidden;
    }

    @include media-breakpoint-down(xs-2) {
      gap: toRem(32);
    }
  }

  .documentation-nav {
    position: sticky;
    z-index: 20;
    top: toRem(132);
    width: calc(261/1160 * 100%);
    min-width: toRem(200);
    display: flex;
    align-self: flex-start;
    flex-direction: column;
    padding: toRem(24) 0;
    box-shadow: 0 toRem(64) toRem(200) toRem(-50) rgba(22, 10, 111, 0.05);
    border-radius: toRem(24);
    background: $color-white;

    @include media-breakpoint-down(lg) {
      top: toRem(142);
    }

    @include media-breakpoint-down(md) {
      position: relative;
      width: 100%;
      overflow: scroll;
      flex-direction: column;
      top: unset;

      -ms-overflow-style: none;
      scrollbar-width: none;

      &::-webkit-scrollbar {
        display: none;
      }
    }

    .nav__menu {
      display: flex;
      flex-direction: column;
      transition: all linear .2s;
      overflow: hidden;
      overflow-y: scroll;
      min-height: toRem(100);
      max-height: calc(100vh - toRem(300));

      &::-webkit-scrollbar {
        width: toRem(8);
      }

      &::-webkit-scrollbar-track-piece {
        background-color: transparent;
        border-radius: toRem(4);
      }

      &::-webkit-scrollbar-thumb {
        background-color: $color-primary;
        border-radius: toRem(4);
      }

      scrollbar-color: $color-primary transparent;
      scrollbar-width: thin;

      @include media-breakpoint-down(md) {
        flex-direction: column;
        width: 100%;
        border: unset;
        overflow: visible;
        border-bottom: none;
      }
    }

    .nav__items {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding: toRem(16) toRem(32);
      gap: toRem(6);
      transition: all linear .2s;
      font-family: $font-family-primary !important;

      @include media-breakpoint-down(md) {
        padding: toRem(16) toRem(16);
        width: 100%;
      }

      &.active {
        background-color: $color-primary;
        color: $color-white !important;
      }
    }
  }

  .documentation-content {
    width: calc(859/1160 * 100%);
    background-color: $color-white;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: toRem(48) toRem(40);
    gap: toRem(32);

    box-shadow: 0px 64px 200px -50px rgba(22, 10, 111, 0.05);
    border-radius: toRem(24);
    overflow: hidden;

    @include media-breakpoint-down(md) {
      width: 100%;
      gap: toRem(24);
      padding: toRem(48) toRem(24);
    }

    .documentation-content__wrap {
      width: 100%;
      display: flex;
      flex-direction: column;
      gap: toRem(32);
      transition: all linear .2s;
      @include media-breakpoint-down(md) {
        gap: toRem(24);
      }
    }
    .documentation-content__content{
      display: flex;
      flex-direction: column;
      gap: toRem(32);
      transition: all linear .2s;
      @include media-breakpoint-down(md) {
        gap: toRem(24);
      }
      ul{
        list-style: disc;
        li{
          margin-left: toRem(24);
        }
      }
      &.documentation-content__content--typography{
        padding: toRem(40) toRem(40) toRem(105);
        background: $color-img-card;
        border-radius: toRem(24);
        @include media-breakpoint-down(xl) {
          padding: toRem(40) toRem(24) toRem(40);
        }
        @include media-breakpoint-down(md) {
          padding: toRem(24);
        }
        .typography__title{          
          font-family: $font-family-secondary;
          font-weight: 500;
          font-size: toRem(48);
          line-height: 1.1;
          letter-spacing: 0.02em;
          text-decoration-line: underline;
        }
        .typography__example{          
          font-family: $font-family-secondary;
          font-weight: 400;
          font-size: toRem(32);
          line-height: 1.5;
          letter-spacing: 0.02em;
          word-wrap: break-word;
        }
        .typography__group{
          margin-top: toRem(30);
          display: grid;
          grid-template-columns: 1fr;
          gap: toRem(24);
          .typography__header{
            display: grid;
            grid-template-columns: toRem(180) repeat(3,1fr);
            gap: toRem(16);
            padding-bottom: toRem(24);
            border-bottom: toRem(4) solid $color-grey-5;
            @include media-breakpoint-down(xl) {
              grid-template-columns: toRem(160) toRem(82) toRem(133) max-content;
            }
            @include media-breakpoint-down(lg) {
              display: none;
            }
          }
          .typography__header--item{
            font-weight: 500;
            font-size: toRem(24);
            line-height: 1.4;
            @include media-breakpoint-down(xl) {
              font-size: toRem(20);
            }
            @include media-breakpoint-down(lg-2) {
              font-size: toRem(18);
            }
          }
          .typography__item{
            display: grid;
            grid-template-columns: toRem(180) repeat(3,1fr);
            gap: toRem(16);
            @include media-breakpoint-down(xl) {
              grid-template-columns: toRem(160) toRem(82) toRem(133) max-content;
            }
            @include media-breakpoint-down(lg) {
              grid-template-columns: 1fr;
            }
            .typography__item--mobile-title{
              margin-bottom: toRem(4);
              display: none;
              font-weight: 400;
              font-size: toRem(16);
              line-height: 1.4;
              @include media-breakpoint-down(lg) {
                display: block;
              }
            }
            .typography__item--h1{
              font-weight: 700;
              font-size: toRem(32);
              line-height: 1.5;
            }
            .typography__item--h2{
              font-weight: 700;
              font-size: toRem(28);
              line-height: 1.5;
            }
            .typography__item--h3{
              font-weight: 500;
              font-size: toRem(24);
              line-height: 1.5;
            }
            .typography__item--body-xl{
              font-weight: 500;
              font-size: toRem(20);
              line-height: 1.4;
            }
            .typography__item--body-lg{
              font-weight: 400;
              font-size: toRem(18);
              line-height: 1.35;
              &-medium{
                font-size: toRem(18);
                line-height: 1.35;
                font-weight: 500;
              }
            }
            .typography__item--body-normal{
              font-weight: 400;
              font-size: toRem(16);
              line-height: 1.5;
              &-bold{
                font-weight: 700;
                font-size: toRem(16);
                line-height: 1.5;
              }
              &-medium{
                font-weight: 500;
                font-size: toRem(16);
                line-height: 1.5;
              }
            }
            .typography__item--body-small{
              font-weight: 400;
              font-size: toRem(14);
              line-height: 1.4;
              &-bold{
                font-weight: 700;
                font-size: toRem(14);
                line-height: 1.4;
              }
              &-medium{
                font-weight: 500;
                font-size: toRem(14);
                line-height: 1.4;
              }
            }
            .typography__item--body-xsmall{
              font-weight: 400;
              font-size: toRem(12);
              line-height: 1.5;
              &-medium{
                font-weight: 500;
                font-size: toRem(12);
                line-height: 1.5;
              }
            }
            .typography__item--text-small{
              font-weight: 500;
              font-size: toRem(14);
              line-height: 1.4;
              letter-spacing: 0.08em;
            }
            .typography__item--text-xsmall{
              font-weight: 500;
              font-size: toRem(12);
              line-height: 1.5;
              letter-spacing: 0.04em;
              text-transform: uppercase;
            }
          }
        }
      }
      &.documentation-content__content--color-style{
        padding: toRem(40);
        background: $color-img-card;
        border-radius: toRem(24);
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: toRem(48);
        @include media-breakpoint-down(xl) {
          padding: toRem(40) toRem(24);
        }
        @include media-breakpoint-down(md) {
          padding: toRem(24);
        }
        .color-style__title{
          font-family: $font-family-primary;
          font-weight: 700;
          font-size: toRem(20);
          line-height: 1.5;
          letter-spacing: -0.04em;
          margin-bottom: toRem(24);
        }
        .color-style__box{
          width: 100%;
        }
        .color-style__group{
          display: grid;
          grid-template-columns: repeat(3,1fr);
          gap: toRem(22);
          width: 100%;
          @include media-breakpoint-down(lg-2) {
            gap: toRem(18);
          }
          @include media-breakpoint-down(lg) {
            grid-template-columns: 1fr;
            gap: toRem(16);
          }
          &.color-style__group--remaining{
            display: grid;
            grid-template-columns: repeat(4,1fr);
            gap: toRem(22);
            width: 100%;
            @include media-breakpoint-down(lg-2) {
              gap: toRem(18);
            }
            @include media-breakpoint-down(lg) {
              grid-template-columns: 1fr;
              gap: toRem(16);
            }
          }
        }
        .color-style__styling{
          margin-top: toRem(13);
          display: flex;
          flex-direction: column;
          gap: toRem(8);
        }
        .color-style__name{
          font-style: normal;
          font-weight: 500;
          font-size: toRem(16);
          line-height: 1.4;
        }
        .color-style__code{
          display: flex;
          flex-direction: row;
          gap: toRem(2.4);
          align-items: center;
          font-weight: 400;
          font-size: toRem(16);
          line-height: 1.4;
          @include media-breakpoint-down(lg-2) {
            font-size: toRem(14);
            line-height: 1.4;
          }
          span{
            opacity: .75;
          }
        }
        .color-style__item--color{
          height: toRem(102);
          border-radius: toRem(22);
          &.color-style__item--royal-blue{
            background-color: $color-primary;
          }
          &.color-style__item--dull-lavender{
            background-color: $color-secondary;
          }
          &.color-style__item--pink-salmon{
            background-color: $color-tertiary;
          }
          &.color-style__item--pastel-pink{
            background-color: $color-accent-1;
          }
          &.color-style__item--hawkes-blue{
            background-color: $color-accent-2;
          }
          &.color-style__item--mint-tulip{
            background-color: $color-accent-3;
          }
          &.color-style__item--black{
            background-color: $color-black;
          }
          &.color-style__item--grey-1{
            background-color: $color-grey-1;
          }
          &.color-style__item--grey-2{
            background-color: $color-grey-2;
          }
          &.color-style__item--grey-3{
            background-color: $color-grey-3;
          }
          &.color-style__item--grey-4{
            background-color: $color-grey-4;
          }
          &.color-style__item--grey-5{
            background-color: $color-grey-5;
          }
          &.color-style__item--white{
            background-color: $color-white;
          }
        }
      }
    }
    .documentation-content__img{
      width: 100%;
      img{
        object-fit: cover;
        width: max-content;
        max-width: 100%;
      }
    }
    a{
      font-weight: 500;
      color: $color-primary;
      transition: all linear .2s;
      &:hover{
        color: $color-secondary;
      }
    }
    .line{
      position: relative;
      width: 100%;
      .line__content{
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: toRem(32);
        width: 100%;
        .line__border{
          display: block;
          width: 100%;
          max-width: toRem(456);
          height: 1px;
          opacity: .2;
          background-color: $color-primary;
        }
      }
    }
    .docs-title{
      font-family: $font-family-primary;
      font-style: normal;
      font-weight: 700;
      font-size: toRem(24);
      line-height: 1.1;
      &--main{
        font-size: toRem(32);
      }
    }
    .docs-subtitle{
      font-family: $font-family-primary;
      font-style: normal;
      font-weight: 700;
      font-size: toRem(16);
      line-height: 1.6;
      letter-spacing: -0.04em;
    }
  }
}