$grid-breakpoints: (
  xs: 0,
  xs-1: 320px,
  xs-2: 375px,
  xs-3: 480px,
  sm: 576px,
  md: 768px,
  lg: 992px,
  lg-2: 1064px,
  xl: 1200px,
  xxl: 1400px,
  xxl-2: 1440px,
);


///////////////////////////////////////
//Colors
//Brand Color
$color-primary: #6A5AE0;
$color-secondary: #9087E5;
$color-tertiary: #FF8FA2;

$color-accent-1: #FFD6DD;
$color-accent-2: #C4D0FB;
$color-accent-3: #C9F2E9;

$color-super-black: #000000;
$color-black: #0C092A;
$color-grey-1: #49465F;
$color-grey-2: #858494;
$color-grey-3: #CCCCCC;
$color-grey-4: #E6E6E6;
$color-grey-5: #EFEEFC;
$color-white: #FFFFFF;
$color-body:#F7F9FC;
$color-img-card:#F3F5FA;
$color-purple-1:#8E7FFB;


$colorsMap: (
  primary: $color-primary,
  secondary: $color-secondary,
  tertiary: $color-tertiary,

  accent-1: $color-accent-1,
  accent-2: $color-accent-2,
  accent-3: $color-accent-3,

  super-black: $color-super-black,
  black: $color-black,
  grey-1: $color-grey-1,
  grey-2: $color-grey-2,
  grey-3: $color-grey-3,
  grey-4: $color-grey-4,
  grey-5: $color-grey-5,
  white: $color-white,
);

@mixin ColorClass {
  @each $name, $color in $colorsMap {
    .color-#{$name} {
      color: $color;  
    }
  }
}

@include ColorClass;

