h1{
  font-family: $font-family-primary;
  font-weight: 700;
  font-size: toRem(80);
  line-height: 1.1;
  letter-spacing: -0.04em;
  @include media-breakpoint-down(md) {
    font-size: toRem(48);
    line-height: 1.1;
  }
}
h2{
  font-family: $font-family-primary;
  font-weight: 700;
  font-size: toRem(64);
  line-height: 1.2;
  letter-spacing: -0.04em;
  @include media-breakpoint-down(md) {
    font-size: toRem(32);
    line-height: 1.2;
  }
}
h3{
  font-family: $font-family-primary;
  font-weight: 700;
  font-size: toRem(50);
  line-height: 1.2;
  letter-spacing: -0.04em;
  @include media-breakpoint-down(md) {
    font-size: toRem(28);
    line-height: 1.2;
  }
}
p{
  font-family: $font-family-secondary;
  font-weight: 400;
}
.text-body-lg{
  font-family: $font-family-secondary;
  font-size: toRem(20);
  line-height: 1.4;
  font-weight: 400;
}
.text-body-lg{
  font-family: $font-family-secondary;
  font-size: toRem(18);
  line-height: 1.35;
  font-weight: 400;
}
.text-body-regular{
  font-family: $font-family-secondary;
  font-size: toRem(16);
  line-height: 1.5;
  font-weight: 400;
}
.text-body-sm{
  font-family: $font-family-secondary;
  font-size: toRem(14);
  line-height: 1.4;
  font-weight: 400;
}