* {
  box-sizing: border-box;
}

html {
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  margin: 0 auto;
  color: $color-grey-2;
  background-color: $color-body;
  font-family: $font-family-secondary;
  overflow-x: hidden;

  &.body-no-overflow {
    overflow: unset;
  }
}

h1,h2,h3,h4,h5,h6,
p {
  margin: 0;
}

h1 {
  font-size: toRem(56);
  font-family: $font-family-primary;
  line-height: 1.2;
  font-weight: 500;

  @include media-breakpoint-down(sm) {
    font-size: toRem(36);
    line-height: 1.3;
  }
}

h2 {
  font-size: toRem(40);
  font-family: $font-family-primary;
  line-height: 1.2;

  @include media-breakpoint-down(sm) {
    font-size: toRem(28);
    line-height: 1.3;
  }
}

h3 {
  font-size: toRem(24);
  font-family: $font-family-primary;
  line-height: 1.1;

  @include media-breakpoint-down(sm) {
    font-size: toRem(20);
    line-height: 1.3;
  }
}

h4 {
  font-size: toRem(20);
  font-family: $font-family-primary;
  line-height: 1.2;
}

h5 {
  font-size: toRem(18);
  font-family: $font-family-primary;
  line-height: 1.2;
}

h6 {
  font-size: toRem(16);
  font-family: $font-family-primary;
  line-height: 1.2;
}

a {
  font-family: $font-family-secondary;
  font-style: normal;
  font-weight: 400;
  text-decoration: none !important;
  color: $color-primary;
  transition: all linear .2s;
  &:hover{
    color: $color-black;
  }
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

// For Pattern and Ornament on All Pages
.pattern,
.ornament {
  pointer-events: none;
}

// Bold
.bold {
  font-weight: 600;
}

// Italic
.italic {
  font-style: italic;
}

// Bullet List
.bullet-list {
  list-style: disc;
}

// Numered List
.numered-list {
  list-style: decimal;
}

.container {
  @include media-breakpoint-up(xl) {
    padding-left: 0;
    padding-right: 0;
  }
  @include media-breakpoint-up(xxl) {
    max-width: toRem(1160) !important;
  }
  @include media-breakpoint-down(xl) {
    max-width: 100% !important;
    padding-left: toRem(40) !important;
    padding-right: toRem(40) !important;
  }
  @include media-breakpoint-down(lg) {
    padding-left: toRem(32) !important;
    padding-right: toRem(32) !important;
  }
  @include media-breakpoint-down(md) {
    padding-left: toRem(20) !important;
    padding-right: toRem(20) !important;
  }
  @include media-breakpoint-down(xs-2) {
    padding-left: toRem(16) !important;
    padding-right: toRem(16) !important;
  }
}