// Just an example


/**
* This is a variable font
* You can controll variable axes as shown below:
* font-variation-settings: 'wght' 900.0;
*
* available axes:

* 'wght' (range from 300.0 to 900.0)

*/

@font-face {
  font-family: 'Satoshi';
  src: url('../../media/fonts/satoshi/Satoshi-Variable.woff2') format('woff2'),
       url('../../media/fonts/satoshi/Satoshi-Variable.woff') format('woff'),
       url('../../media/fonts/satoshi/Satoshi-Variable.ttf') format('truetype');
       font-weight: 300 900;
       font-display: swap;
       font-style: normal;
}


